// Display Video
var EmbedVideo = function(props) {
  return (
      <div dangerouslySetInnerHTML={{ __html: `
       <video
         loop
         muted
         autoplay
         playsinline
         src="${props.src}"
         class="${props.className}"
         id="${props.id}"
       />,
     ` }}></div>
  )
}

